import md5 from '../../static/js/md5.js';
import rs from '../../static/js/request.js';
let {
	log
} = console;
const app = getApp().globalData;
const {
	appid,
	appsecret,
	imgRemote,
	navBar
} = app;
export default {
	data() {
		return {
			address: '',
			contact: '',
			details: '',
			mobile: '',
			childzid: '',
			navBar: navBar,
			lat:'',
			lng:''
		};
	},
	methods: {
		leftClick() {
			uni.navigateBack({
				delta: 1
			});
		},
		mapPage() {






			var that = this;
			uni.chooseLocation({
				success: function(res) {
					// console.log('位置名称：' + res.name);
					// console.log('详细地址：' + res.address);
					// console.log('纬度：' + res.latitude);
					// console.log('经度：' + res.longitude);
					that.address = res.address
					var obj = that.tobdMap(res.longitude,res.latitude);
					that.lng = obj.lng;
					that.lat = obj.lat;
				}
			});
		},
		tobdMap(lng, lat) {
			let x_pi = 3.14159265358979324 * 3000.0 / 180.0;
			      let x = lng;
			      let y = lat;
			      let z = Math.sqrt(x * x + y * y) + 0.00002 * Math.sin(y * x_pi);
			      let theta = Math.atan2(y, x) + 0.000003 * Math.cos(x * x_pi);
			      let lngs = z * Math.cos(theta) + 0.0065;
			      let lats = z * Math.sin(theta) + 0.006;
			     
			      return {
			          lng: lngs,
			          lat: lats 
			      } 
		},
		memberAddressInfo() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp
			};

			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				select_zid: this.childzid,
				sign: sign
			}, obj)
			rs.getRequests("memberAddressInfo", params, (res) => {
				// console.log(res)
				if (res.data.code == 200) {
					let data = res.data.data;
					this.contact = data.contact;
					this.mobile = data.phone;
					this.address = data.address;
					this.details = data.details;
				}
			})
		},
		submit() {
			let timeStamp = Math.round(new Date().getTime() / 1000);
			let obj = {
				appid: appid,
				timeStamp: timeStamp,
				contact: this.contact,
				mobile: this.mobile,
				address: this.address,
			};
			let sign = md5.hexMD5(rs.objKeySort(obj) + appsecret);
			let params = Object.assign({
				sign: sign,
				details: this.details,
				latitude: this.lat,
				longitude: this.lng
			}, obj)
			rs.postRequests("updateAddress", params, (res) => {
				if (res.data.code == 200) {
					rs.Toast('保存成功');
					setTimeout(() => {
						this.leftClick();
					}, 1000);

				} else {
					rs.Toast(res.data.msg);
				}

			})
		},
	},
	onLoad(option) {
		this.childzid = option.childzid;
		this.contact = option.contact;
		this.mobile = option.mobile;
		this.address = option.address;
		this.details = option.details;
		let count = option.count || 1;
		// console.log(option.count)
		if (count == 1) {
			if (this)
				this.memberAddressInfo()
		}
	},



};
